<template>
    <div class="DataField">
        <div class="DataField__field">{{ data.name }}</div>

        <div class="DataField__field" v-if="!isEditMode">
            <select v-if="data.options" @change="onSelectChange">
                <option
                    v-for="option in data.options"
                    :value="option"
                    :key="option"
                    :selected="option === data.value"
                >
                    {{ option }}
                </option>
            </select>
            <span v-else>{{ value }}</span>
        </div>

        <input v-else v-model="value"/>

        <button v-if="data.canEdit" @click="toggleEditMode">
            {{ buttonName }}
        </button>
    </div>
</template>

<script>
export default {
    name: 'DataField',

    props: {
        data: {
            type: Object,
            required: true
        }
    },

    data() {
        return {
            isEditMode: false,
            value: this.data.value
        }
    },

    computed: {
        buttonName() {
            return this.isEditMode ? 'save' : 'edit';
        }
    },

    methods: {
        toggleEditMode() {
            if (this.isEditMode && this.value !== this.data.value) {
                this.$emit('updateField', this.value)
            }
            this.isEditMode = !this.isEditMode;
        },
        onSelectChange(event) {
            if (event.target.value === '-') {
              return;
            }

            this.$emit('updateField', event.target.value);
        }
    }
}
</script>

<style lang="scss" scoped>
.DataField {
    border-bottom: 1px solid black;
    display: flex;
    padding: 20px 10px;

    &__field {
        flex: 1;
    }
}
</style>